.tooltipContainer {
    position: relative;
    display: inline-block;
}

.passwordText {
    visibility: hidden;
    width: 300px;
    background-color: #555;
    color: #fff;
    text-align: center;
    padding: 5px 0;
    border-radius: 6px;

    /* Position the tooltip text */
    position: absolute;
    z-index: 1;
    bottom: -255%;
    left: 200%;
    margin-left: -60px;

    /* Fade in tooltip */
    opacity: 0;
    transition: opacity 0.3s;
}

.tooltipContainer:hover .passwordText {
    visibility: visible;
    opacity: 1;
}

